

    .left {
        text-align: left;
        @media #{$media-size-phone} {
          text-align: center;
        }
  
        .title {
          font-family: "Montserrat", sans-serif;
  
          h1, h2 {
            font-size: 97px;
            line-height: 1.1em;
            margin: 0px;
            display: block;
            @media #{$media-size-phone} {
              font-size: 40px;
            }
          }
  
          h1 {
            font-weight: 300;
          }
  
          h2 {
            font-weight: 700;
          }
        }
  
        .underline {
          width: 100px;
          height: 6px;
          margin: 15px 0px;
  
          [data-theme=dark] & {
            background: #ffffff;
          }
  
          [data-theme=light] & {
            background: #c5c5c5;
          }
  
          @media #{$media-size-phone} {
            margin: 15px auto;
          }
        }
  
        .subtitle {
          margin: 0px;
          font-size: 32px;
          font-weight: 200;
        }
      }
  
      .doge-container {
        position: relative;
        width: 384px;
        height: 384px;
        overflow: hidden;
        border-radius: 50%;
        display: flex;
        justify-content: center;
  
        [data-theme=dark] & {
          background: #000000;
        }
  
        [data-theme=light] & {
          background: #dbdbdb;
        }
  
        &.mobile {
          display: none;
        }
  
        @media #{$media-size-phone} {
          width: 150px;
          height: 150px;
          &.desktop {
            display: none;
          }
          &.mobile {
            display: block;
          }
        }
  
        .kabosu {
          z-index: 3;
          position: relative;
          top: 60px;
          width: 300px;
          height: 372px;
          @media #{$media-size-phone} {
            top: 20px;
            left: 15px;
            width: 124px;
            height: 148px;
          }
        }
  
        #left-eye, #right-eye, #nose {
          z-index: 2;
          position: absolute;
        }
  
        .eyes-background {
          position: absolute;
          z-index: 1;
          left: 150px;
          top: 145px;
          width: 130px;
          height: 50px;
          background-color: #E4D79A;
        }
  
        #left-eye {
          top: 171px;
          left: 166px;
          width: 20px;
          height: auto;
        }
  
        #right-eye {
          top: 157px;
          left: 243px;
          width: 20px;
          height: auto;
        }
  
        #nose {
          z-index: 4;
          cursor: pointer;
          top: 200px;
          left: 245px;
          width: 40px;
          height: 33px;
          border-radius: 50%;
        }
  
        // Design found here: https://codepen.io/quadbaup/pen/rKOKQv
        .thought {
          font-family: "Montserrat", sans-serif;
          font-weight: bold;
          z-index: 5;
          display: none;
          background-color: #fff;
          padding: 20px;
          border-radius: 30px;
          min-width: 40px;
          max-width: 220px;
          min-height: 40px;
          margin: 20px;
          position: absolute;
          align-items: center;
          justify-content: center;
          text-align: center;
  
          &.visible {
            display: flex;
          }
        }
  
        .thought::before,
        .thought::after {
          content: "";
          background-color: #fff;
          border-radius: 50%;
          display: block;
          position: absolute;
          z-index: -1;
        }
  
        .thought::before {
          width: 44px;
          height: 44px;
          top: -12px;
          left: 28px;
          box-shadow: -50px 30px 0 -12px #fff;
        }
  
        .thought::after {
          bottom: -10px;
          right: 26px;
          width: 30px;
          height: 30px;
          box-shadow: 40px -34px 0 0 #fff,
          -28px -6px 0 -2px #fff,
          -24px 17px 0 -6px #fff,
          -5px 25px 0 -10px #fff;
  
        }
      }
  
      @media #{$media-size-phone} {
        flex-direction: column-reverse;
      }


@keyframes rainbow {
    100%, 8% {
      background-color: rgb(255, 127, 0);
    }
    16% {
      background-color: rgb(255, 255, 0);
    }
    25% {
      background-color: rgb(127, 255, 0);
    }
    33% {
      background-color: rgb(0, 255, 0);
    }
    41% {
      background-color: rgb(0, 255, 127);
    }
    50% {
      background-color: rgb(0, 255, 255);
    }
    58% {
      background-color: rgb(0, 127, 255);
    }
    66% {
      background-color: rgb(0, 0, 255);
    }
    75% {
      background-color: rgb(127, 0, 255);
    }
    83% {
      background-color: rgb(255, 0, 255);
    }
    91% {
      background-color: rgb(255, 0, 127);
    }
  }