.home {
  .main {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    max-width: $max-width;

    [data-theme="dark"] & {
      color: #ffffff;
    }
  }

  @mixin coverer {
    height: 100vh;
    position: absolute;
    top: 0;
    left: 0;
  }

  // Small devices (landscape phones, 576px to M)
  @media (max-width: 991px) {
    video {
      object-fit: cover;
      width: 100vw;
      height: 100vh;
      position: fixed;
      top: 0;
      left: 0;
      visibility: visible;
    }
  }

  // Medium devices (tablets, 768px to L)
  @media (min-width: 992px) {
    video {
      object-fit: cover;
      object-position: 100% 100%;
      width: 100vw;
      height: 100vh;
      position: fixed;
      top: 0;
      left: 0;
      visibility: visible;
    }
  }



  video.scrolled {
    visibility: hidden;
  }

  .viewport-header {
    position: relative;
    height: 100vh;
    text-align: center;
    margin-top: -100px;
    display: flex;
    align-items: center;
    justify-content: center;
  }

  .title {
    font-family: "Montserrat", sans-serif;
  }

  h1,
  h2 {
    font-size: 97px;
    line-height: 1.1em;
    margin: 0px;
    display: block;
    @media #{$media-size-phone} {
      font-size: 40px;
    }
  }

  h1 {
    font-size: 6.9vw;
    font-weight: 700;
    color: #ffffff;
    top: 50%;
  }

  h2 {
    font-size: 45px;
    line-height: 1.1em;
    margin: 0px;
    display: block;
    letter-spacing: -1px;
    @media #{$media-size-phone} {
      font-size: 40px;
    }
    color: #c5c5c5;
  }

  h2 {
    font-weight: 700;
  }

  @mixin cover($i, $url) {
    &:nth-child(#{$i}) {
      &:after {
        background-image: url($url);
      }
    }
  }

  .underline {
    width: 100px;
    height: 6px;
    margin: 15px 0px;

    [data-theme="dark"] & {
      background: #ffffff;
    }
  }

  main {
    [data-theme="dark"] & {
      background: #000000;
    }

    [data-theme="light"] & {
      background: white;
    }
    position: relative;
    padding: 1rem;
    &::before {
      content: "";
      @include coverer;
    }
    p {
      max-width: 100vw;
      margin: 1rem auto;
    }
  }

  .font28 {
    font-size: 28px;
  }

  .screen {
    position: relative;
    height: 100%;
    width: 100vw;
    letter-spacing: 1px;
  }

  .preload {
    position: relative;
    transform: translateY(-50%);
    font-size: 1.6vw;
    line-height: 5px;
    text-align: center;
    text-transform: lowercase;
  }

  .preload__letter {
    display: inline-block;
    margin: 0 0px;
    position: relative;
    letter-spacing: -5px;
    text-transform: lowercase;
  }

  .preload__letter--loaded::after {
    content: "";
    width: 100%;
    text-align: center;
    position: relative;
    top: 50%;
    left: 0;
    color: #fff;
    font-size: 18px;
    line-height: 12px;
  }

  .d_text_display {
    position: relative;
    overflow: hidden;
    font-size: 5vw;
  }

  .d_text_display #dynamic_d {
    opacity: 1;
    left: 0;
    bottom: 0;
    border-radius: 10px;
    transition: 0.5s;
    transform: translateY(100%) scale(1.2);
    visibility: visible;
  }

  .d_text_display.for_animation_class #dynamic_d {
    transform: translateY(0%) scale(1);
    visibility: visible;
  }

  .dogelogo{
    margin: auto;
    min-width: 35vw;
    cursor: pointer;
    padding-right: 25px;
  }

  @media (max-width: 768px) {
    .preload {
      font-size: 20px;
      top: 55%;
    }

    .dogelogo{
      max-width: 80vw;
    }

    .d_text_display {
      font-size: 13vw;
    }
  }
  @media (min-width:768px) and (max-width:1200px){
    .dogelogo{
      max-width: 50vw;
    }

    .d_text_display {
      font-size: 6vw;
    }
  }
  @media (min-width: 1200px) {
    .fs-3 {
        font-size: 1.55rem !important;
        line-height: 35px;
    }
  }

}