.accordion-button {
    width: 100% !important;
    padding-left: 2em !important;
    align-items: left !important;
    justify-content: flex-start !important;
}

.accordion-button:after {
    position: absolute;
    order: -1; //swap order
    margin-right:.5em !important; // just extra space between text and icon
    left: 0vw !important;
}
